.form-content {
  &__task-description {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }
  &__task-input {
    width: calc(50% - 16px);
    margin: 0 8px;
    margin-bottom: 16px;
  }
  &__task-select {
    width: calc(50% - 16px);
    margin: 0 8px;
    margin-bottom: 16px;
  }
  &__long-input {
    margin: 0 8px 16px 8px;
  }
  &__task-search {
    display: flex;
    width: calc(50% - 16px);
    justify-content: space-between;
    margin: 0 8px;
    margin-bottom: 16px;
  }
  &__search-input {
    width: 100%;
  }
  &__task-search-btn {
    margin-top: 29px;
    margin-left: 4px;
  }
  &__date {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }
  &__creator {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }
  &__btn {
    margin-left: 4px;
  }
}

@media (max-width: 991px) {
  .form-content {
    &__task-input {
      width: 100%;
    }
    &__task-search {
      width: 100%;
    }
    &__task-select {
      width: 100%;
    }
  }
}
